<template>
    <div class="titleContainer shadow">
        <div class="title">
            <h1>Pedagog</h1>
        </div>
    </div>

    <div class="wydarzenie shadow">
        <div class="description">
            <h2>Informacje</h2>
            <span>
                <b>Godziny pracy pedagoga:</b><br />
                <span>Poniedziałek: 9:10 - 14:10</span><br />
                <span>Wtorek: 8:00 - 13:20</span><br />
                <span>Środa: 8:20 - 13:20</span><br />
                <span>Czwartek: 10:30 - 14:10</span><br />
                <span>Piątek: 8:45 - 11:45</span><br />
            </span>
            <span>
                <b>Pani pedagog: </b><span>mgr Agata Woźniak</span><br />
            </span>
            <span>
                Główne kierunki pracy pedagoga szkolnego:
            </span><br />

            <span>{{ page }} / {{ numPages }}</span><br />
            <!--Przyciski-->
            <button @click="leftPage();" class="przycisk shadow">
                <i class="fa-solid fa-caret-left"></i>
            </button>
            <button @click="rightPage();" class="przycisk shadow">
                <i class="fa-solid fa-caret-right"></i>
            </button>

            <!--PDF-->
            <div class="pdf">
                <pdf src="./pdf/plan-pracy-pedagog.pdf" :page="this.page" />
            </div>
            
            <!--Przyciski-->
            <button @click="leftPage();" class="przycisk shadow">
                <i class="fa-solid fa-caret-left"></i>
            </button>
            <button @click="rightPage();" class="przycisk shadow">
                <i class="fa-solid fa-caret-right"></i>
            </button>
        </div>
    </div>
</template>

<script>
import pdf from 'pdfvuer';

export default {
    name: 'Pedagog',
    data(){
        return{
            page: 1,
            numPages: 8
        }
    },
    components: {
        pdf,
    },
    methods:{
        leftPage(){
            if(this.page !== 1)
            {
                this.page--
            }
        },

        rightPage(){
            if(this.page !== this.numPages)
            {
                this.page++
            }
        }
    }
}
</script>