                <span>- Od 1509r. - szkołę utrzymywał miejscowy proboszcz (zgodnie z rozporządzeniem bp. krakowskiego - Jana Konarskiego). Najprawdopodobniej kształcono w niej uczniów w duchu religijnym.</span><br />
