<template>
    <div class="titleContainer shadow">
        <div class="title">
            <h1>Lekcje Zdalne</h1>
        </div>
    </div>

    <div class="wydarzenie shadow">
        <div class="description">
            <h2>Regulamin Edukacji Zdalnej</h2>
            <span>{{ page }} / {{ numPages }}</span><br />

            <!--Przyciski-->
            <button @click="leftPage();" class="przycisk shadow">
                <i class="fa-solid fa-caret-left"></i>
            </button>
            <button @click="rightPage();" class="przycisk shadow">
                <i class="fa-solid fa-caret-right"></i>
            </button>

            <!--PDF-->
            <div class="pdf">
                <pdf src="./pdf/Regulamin-edukacji-zdalnej.pdf" :page="this.page" />
            </div>
            
            <!--Przyciski-->
            <button @click="leftPage();" class="przycisk shadow">
                <i class="fa-solid fa-caret-left"></i>
            </button>
            <button @click="rightPage();" class="przycisk shadow">
                <i class="fa-solid fa-caret-right"></i>
            </button>






            <h2>Procedura Edukacji Zdalnej</h2>
            <span>{{ page2 }} / {{ numPages2 }}</span><br />

            <!--Przyciski-->
            <button @click="leftPage2();" class="przycisk shadow">
                <i class="fa-solid fa-caret-left"></i>
            </button>
            <button @click="rightPage2();" class="przycisk shadow">
                <i class="fa-solid fa-caret-right"></i>
            </button>

            <!--PDF-->
            <div class="pdf">
                <pdf src="./pdf/Procedury-zdalne_nauczanie.pdf" :page="this.page2" />
            </div>
            
            <!--Przyciski-->
            <button @click="leftPage2();" class="przycisk shadow">
                <i class="fa-solid fa-caret-left"></i>
            </button>
            <button @click="rightPage2();" class="przycisk shadow">
                <i class="fa-solid fa-caret-right"></i>
            </button>




            <h2>Procedura Edukacji Zdalnej</h2>
            <span>{{ page3 }} / {{ numPages3 }}</span><br />

            <!--Przyciski-->
            <button @click="leftPage3();" class="przycisk shadow">
                <i class="fa-solid fa-caret-left"></i>
            </button>
            <button @click="rightPage3();" class="przycisk shadow">
                <i class="fa-solid fa-caret-right"></i>
            </button>

            <!--PDF-->
            <div class="pdf">
                <pdf src="./pdf/zasady-ocenianie.pdf" :page="this.page3" />
            </div>
            
            <!--Przyciski-->
            <button @click="leftPage3();" class="przycisk shadow">
                <i class="fa-solid fa-caret-left"></i>
            </button>
            <button @click="rightPage3();" class="przycisk shadow">
                <i class="fa-solid fa-caret-right"></i>
            </button>
        </div>
    </div>
</template>

<script>
import pdf from 'pdfvuer';

export default {
    name: 'regulEdu',
    data(){
        return{
            page: 1,
            numPages: 3,


            page2: 1,
            numPages2: 8,

            
            page3: 1,
            numPages3: 4
        }
    },
    components: {
        pdf,
    },
    methods:{
        leftPage(){
            if(this.page !== 1)
            {
                this.page--
            }
        },

        rightPage(){
            if(this.page !== this.numPages)
            {
                this.page++
            }
        },


        
        leftPage2(){
            if(this.page2 !== 1)
            {
                this.page2--
            }
        },

        rightPage2(){
            if(this.page2 !== this.numPages2)
            {
                this.page2++
            }
        },



        leftPage3(){
            if(this.page3 !== 1)
            {
                this.page3--
            }
        },

        rightPage3(){
            if(this.page3 !== this.numPages3)
            {
                this.page3++
            }
        }
    }
}
</script>