<template>
    <div class="titleContainer shadow">
        <div class="title">
            <h1>Wycieczki</h1>
        </div>
    </div>

    <div class="wydarzenie shadow">
        <div class="description">
            <h2>Procedura organizowania wycieczek szkolnych w SZKOLE PODSTAWOWEJ nr 13 w Zawierciu</h2>
            
            <h3>Z dn. 13.09.2018 r.</h3>
            <div class="pracaBiblio">
                <h3>Podstawa prawna:</h3>
                <span>- Rozporządzenie Ministra Edukacji Narodowej z dnia 26 lipca 2018 r. zmieniające rozporządzenie w sprawie warunków i sposobu organizowania przez publiczne przedszkola,  szkoły i placówki krajoznawstwa i turystyki Na podstawie art. 22 ust. 2 pkt 12 ustawy z dnia 7 września 1991 r. o systemie oświaty (Dz. U. z 2018 r. poz. 1457)</span><br />
                <span>- Rozporządzenie Ministra Edukacji Narodowej z dnia 25 maja 2018 r. w sprawie warunków i sposobu organizowania przez publiczne przedszkola, szkoły i placówki krajoznawstwa i turystyki Na podstawie art. 47 ust. 1 pkt 8 ustawy z dnia 14 grudnia 2016 r.- Prawo oświatowe (Dz.U z 2018 r. poz. 996 i 1000)</span><br />
                <span>- Prawo oświatowe (Dz. U. z 2018 r. poz. 996 i 1000) art. 47 ust. 1 pkt 8 ustawy z dnia 14 grudnia 2016 r.</span><br />
                <span>- Rozporządzenie Ministra Edukacji Narodowej i Sportu z dnia 31 grudnia 2002 r. w sprawie bezpieczeństwa i higieny w publicznych i niepublicznych szkołach i placówkach (Dz. U. z 2003 r.,  nr 6, poz. 69 ze zmianami)</span><br />
                <span>- ROZPORZĄDZENIE RADY MINISTRÓW Z DNIA 6 MAJA 1997 ROKU w sprawie określenia warunków bezpieczeństwa osób przebywających w górach, pływających, kąpiących się i uprawiających sporty wodne.</span><br />
            </div>


            <div class="pracaBiblio">
                <h3>§1:</h3>
                <span>- Szkoła Podstawowa nr 13  może organizować dla uczniów krajoznawstwo i turystykę.</span><br />
                <span>- W organizowaniu krajoznawstwa i turystyki szkoła może współdziałać ze stowarzyszeniami i innymi podmiotami, których przedmiotem działalności jest krajoznawstwo i turystyka.</span><br />
            </div>


            <div class="pracaBiblio">
                <h3>§2:</h3>
                <h4>Organizowanie przez Szkołę Podstawową nr 13 krajoznawstwa i turystyki ma na celu:</h4>
                <span>- Poznawanie kraju, jego środowiska przyrodniczego, tradycji, zabytków kultury i historii.</span><br />
                <span>- Poznawanie kultury i języka innych państw.</span><br />
                <span>- Poszerzanie wiedzy z różnych dziedzin życia społecznego, gospodarczego i kulturalnego.</span><br />
                <span>- Wspomaganie rodziny i szkoły w procesie wychowania.</span><br />
                <span>- Upowszechnianie wśród uczniów zasad ochrony środowiska naturalnego oraz wiedzy o składnikach i funkcjonowaniu rodzimego środowiska przyrodniczego, a także umiejętności korzystania z zasobów przyrody.</span><br />
                <span>- Upowszechnianie zdrowego stylu życia i aktywności fizycznej oraz podnoszenie sprawności fizycznej.</span><br />
                <span>- Poprawę stanu zdrowia uczniów pochodzących z terenów zagrożonych ekologicznie.</span><br />
                <span>- Przeciwdziałanie zachowaniom ryzykownym, w szczególności w ramach profilaktyki uniwersalnej.</span><br />
                <span>- Poznawanie zasad bezpiecznego zachowania się w różnych sytuacjach.</span><br />
            </div>

            <div class="pracaBiblio">
                <h3>§3:</h3>
                <span>Krajoznawstwo i turystyka są organizowane w trakcie roku szkolnego, w szczególności w ramach odpowiednio zajęć dydaktyczno-wychowawczych lub opiekuńczych, z wyjątkiem okresu ferii letnich i zimowych oraz wiosennej i zimowej przerwy świątecznej.</span><br />
            </div>

            <div class="pracaBiblio">
                <h3>§4:</h3>

                <h4>Krajoznawstwo i turystyka są organizowane w formie wycieczek:</h4>

                <span>- Przedmiotowych – inicjowanych i realizowanych przez nauczycieli w celu uzupełnienia programu wychowania przedszkolnego albo programu nauczania w ramach jednego lub kilku przedmiotów.</span><br />
                <span>- Krajoznawczo-turystycznych o charakterze interdyscyplinarnym, w których udział nie wymaga od uczniów przygotowania kondycyjnego i umiejętności posługiwania się specjalistycznym sprzętem, organizowanych w celu nabywania wiedzy o otaczającym środowisku i umiejętności zastosowania tej wiedzy w praktyce.</span><br />
                <span>- Specjalistycznych wycieczek krajoznawczo-turystycznych, w których udział wymaga od uczniów przygotowania kondycyjnego, sprawnościowego i umiejętności posługiwania się specjalistycznym sprzętem, a program wycieczki przewiduje intensywną aktywność turystyczną, fizyczną lub długodystansowość na szlakach turystycznych.</span><br />

                <h4>Wycieczki mogą być organizowane w kraju lub za granicą.</h4>
            </div>

            <div class="pracaBiblio">
                <h3>§5:</h3>
                <h4>Organizację i program wycieczki dostosowuje się do wieku, zainteresowań i potrzeb uczniów, ich stanu zdrowia, kondycji, sprawności fizycznej i umiejętności.</h4>
            </div>

            <div class="pracaBiblio">
                <h3>§6:</h3>
                <span>- Zgodę na zorganizowanie wycieczki wyraża dyrektor szkoły, zatwierdzając kartę wycieczki.</span><br />
                <span>- Do karty wycieczki dołącza się listę uczniów biorących udział w wycieczce, zawierającą imię i nazwisko ucznia oraz telefon rodzica lub rodziców ucznia listę uczniów podpisuje dyrektor szkoły.</span><br />
            </div>

            <div class="pracaBiblio">
                <h3>§7:</h3>

                <h4>W przypadku wycieczki organizowanej za granicą:</h4>

                <span>- Wycieczki zagraniczne w szkole mogą być organizowane w formach, o których mowa w §4.</span><br />
                <span>- Kierownik wycieczki przedstawia program wyjazdu dyrektorowi szkoły co najmniej na miesiąc przed planowanym terminem wycieczki.</span><br />
                <span>- Dyrektor szkoły jest obowiązany poinformować organ prowadzący i organ sprawujący nadzór pedagogiczny o organizowaniu takiej wycieczki, przekazując kartę wycieczki drogą pocztową lub złożyć na co najmniej 14 dni przed planowanym wyjazdem w trzech egzemplarzach w punkcie kancelaryjnym Delegatury w Sosnowcu.</span><br />
                <span>- Szkoła ma obowiązek zawrzeć umowę ubezpieczenia od następstw nieszczęśliwych wypadków i kosztów leczenia na rzecz osób biorących udział w wycieczce, o ile obowiązek zawarcia takiej umowy nie wynika z odrębnych przepisów.</span><br />
                <span>- Kierownik wycieczki lub co najmniej jeden opiekun wycieczki zna język obcy na poziomie umożliwiającym porozumiewanie się w krajach tranzytowych i kraju docelowym.</span><br />
            </div>

            <div class="pracaBiblio">
                <h3>§8:</h3>
                <span>- Zgoda rodziców na udział w wycieczce ucznia niepełnoletniego jest wyrażana w formie pisemnej.</span><br />
            </div>
            
            <div class="pracaBiblio">
                <h3>§9:</h3>
                <span>- Dyrektor szkoły wyznacza kierownika i opiekunów wycieczki spośród pracowników pedagogicznych szkoły.</span><br />
                <span>- W zależności od celu i programu wycieczki opiekunem wycieczki może być także osoba niebędąca pracownikiem pedagogicznym szkoły, wyznaczona przez dyrektora szkoły.</span><br />
                <span>- W przypadku specjalistycznej wycieczki krajoznawczo-turystycznej, o której mowa w § 4 ust. 1 pkt 3, kierownik i opiekunowie wycieczki są obowiązani posiadać udokumentowane przygotowanie zapewniające bezpieczną realizację programu wycieczki.</span><br />
            </div>

            <div class="pracaBiblio">
                <h3>§10:</h3>
                <h4>Kierownik wycieczki:</h4>
                <span>- Opracowuje program i regulamin wycieczki.</span><br />
                <span>- Zobowiązany jest do przedstawienia następujących dokumentów:</span><br />
                <span>1) Wypełnioną oraz podpisaną przez dyrektora kartę wycieczki.</span><br />
                <span>2) Listę uczniów biorących udział w wycieczce.</span><br />
                <span>3) Pisemne zgody rodziców.</span><br />
                <span>4) Regulamin z podpisami uczniówegulamin z podpisami uczniów.</span><br />
                <span>5) Oświadczenie kierownika wycieczki.</span><br />
                <span>- Zapoznaje uczniów, rodziców i opiekunów wycieczki z programem i regulaminem wycieczki oraz informuje ich o celu i trasie wycieczki.</span><br />
                <span>- Zapewnia warunki do pełnej realizacji programu wycieczki i przestrzegania jej regulaminu oraz sprawuje nadzór w tym zakresie.</span><br />
                <span>- Zapoznaje uczniów i opiekunów wycieczki z zasadami bezpieczeństwa oraz zapewnia warunki do ich przestrzegania.</span><br />
                <span>- Określa zadania opiekunów wycieczki w zakresie realizacji programu wycieczki oraz zapewnienia opieki i bezpieczeństwa uczniom.</span><br />
                <span>- Nadzoruje zaopatrzenie uczniów i opiekunów wycieczki w odpowiedni sprzęt, wyposażenie oraz apteczkę pierwszej pomocy.</span><br />
                <span>- Organizuje i nadzoruje transport, wyżywienie i noclegi dla uczniów i opiekunów wycieczki.</span><br />
                <span>- Dokonuje podziału zadań wśród uczniów.</span><br />
                <span>- Dysponuje środkami finansowymi przeznaczonymi na organizację wycieczki.</span><br />
                <span>- Dokonuje podsumowania, oceny i rozliczenia finansowego wycieczki.</span><br />
            </div>

            <div class="pracaBiblio">
                <h3>§11:</h3>
                <h4>Opiekun wycieczki:</h4>
                <span>- Sprawuje opiekę nad powierzonymi mu uczniami.</span><br />
                <span>- Współdziała z kierownikiem wycieczki w zakresie realizacji programu wycieczki i przestrzegania jej regulaminu.</span><br />
                <span>- Sprawuje nadzór nad przestrzeganiem regulaminu wycieczki przez uczniów, ze szczególnym uwzględnieniem zasad bezpieczeństwa.</span><br />
                <span>- Nadzoruje wykonywanie zadań przydzielonych uczniom.</span><br />
                <span>- Wykonuje inne zadania zlecone przez kierownika wycieczki.</span><br />
            </div>
            
            <div class="pracaBiblio">
                <h3>§12:</h3>
                <span>- Dyrektor szkoły może wyrazić zgodę na łączenie funkcji kierownika i opiekuna wycieczki.</span><br />
            </div>
            
            <div class="pracaBiblio">
                <h3>§13:</h3>
                <h4>Zasady organizacji wycieczek</h4>
                <span>- Dla zapewnienia uczestnikom wycieczek pełnego bezpieczeństwa poza terenem szkoły obowiązuje następująca liczba opiekunów:</span><br />
                <span>1) Wyjścia, wyjazdy do kina, teatru, muzeum i innych docelowych  instytucji, miejsc na terenie Zawiercia – co najmniej 1 opiekun na 30 uczniów.</span><br />
                <span>2) Wycieczki piesze miejskimi szlakami w Zawierciu z korzystaniem ze środków transportu miejskiego- 1 opiekun na 15 osób.</span><br />
                <span>3) Wycieczki autokarowe poza teren Zawiercia – 1 opiekun na 15 osób.</span><br />
                <span>4) Wcieczki zagraniczne – 1 opiekun na 10 osób.</span><br />
                <span>5) Wycieczki turystyki kwalifikowanej – 1 opiekun na 8 osób.</span><br />
                <span>6) Wyjazdy na tzw. zielone szkoły – 1 opiekun na 10 osób.</span><br />
                <span>7) Wycieczki rowerowe: 2 opiekunów na grupę 10–13 uczniów (zgodnie z prawem o ruchu drogowym w kolumnie rowerów nie może jechać więcej niż 15 osób); wszyscy uczniowie muszą posiadać kartę rowerową.</span><br />
                <span>8) W szczególnych przypadkach dyrektor może zwiększyć liczbę opiekunów.</span><br />
                <span>- Niedopuszczalne jest realizowanie wycieczek podczas burzy, śnieżycy, gołoledzi.</span><br />
                <span>- Miejscem zbiórki uczniów przed planowaną wycieczką i po niej jest teren szkolny.</span><br />
                <span>- Przed wyjazdem (wyjściem) na wycieczkę, należy pouczyć jej uczestników o zasadach bezpieczeństwa.</span><br />
                <span>- Opiekunowie sprawdzają stan liczebny uczestników przed wyruszeniem z każdego miejsca pobytu, w czasie przejazdu oraz po przybyciu do miejsca docelowego, zabierają apteczkę pierwszej pomocy.</span><br />
                <span>- Udział uczniów w wycieczce/wyjściu wymaga pisemnej zgody rodziców:</span><br />
                <span>1) Jeżeli jest to krótkie wyjście poza teren szkoły (w pobliżu szkoły), w ramach nauczanego przedmiotu podczas lekcji, należy zgłosić dyrektorowi lub jego zastępcy zamiar wyjścia i dokonać odpowiedniego wpisu w rejestrze wyjść grupowych uczniów.</span><br />
                <span>2) Rejestr wyjść grupowych poza teren szkoły znajduje się w sekretariacie szkoły.</span><br />
                <span>- Podczas wycieczek należy bezwzględnie przestrzegać zasad bezpiecznego poruszania się po drogach.</span><br />
                <span>- Przy organizowaniu wycieczek autokarowych należy uwzględnić posiadanie przez kierowcę aktualnego przeglądu  stanu technicznego pojazdu.</span><br />
            </div>
            
            <div class="pracaBiblio">
                <h3>§14:</h3>
                <h4>Obowiązki uczestników wycieczki</h4>
                <span>- Uczestnik wycieczki jest zobowiązany:</span><br />
                <span>1) Posiadać legitymację szkolną.</span><br />
                <span>2) Przybyć na miejsce zbiórki o wyznaczonej godzinie.</span><br />
                <span>3) Poinformować kierownika wycieczki o pojawiających się stanach złego samopoczucia.</span><br />
                <span>4) Wykonywać polecenia kierownika, opiekunów i przewodnika.</span><br />
                <span>5) W autokarze zająć miejsce wyznaczone przez opiekuna i nie przemieszczać się, nie spożywać posiłków ani napojów w czasie jazdy.</span><br />
                <span>6) Nie zaśmiecać pojazdu.</span><br />
                <span>7) W czasie postoju i zwiedzania nie oddalać się od grupy bez zgody opiekuna.</span><br />
                <span>8) Posiadać odpowiedni do charakteru wycieczki strój, zwłaszcza obuwie.</span><br />
                <span>9) W czasie przebywania w obiektach noclegowych przestrzegać postanowień regulaminów tych obiektów.</span><br />
                <span>10) Stosować się do wcześniejszych ustaleń.</span><br />
            </div>

            <div class="pracaBiblio">
                <h3>§15:</h3>
                <h4>Finansowanie wycieczek</h4>
                <span>- Plan finansowy wycieczki musi określać ogólny koszt wycieczki, koszt jednego uczestnika oraz przewidywane koszty organizacyjne i programowe.</span><br />
                <span>- Działalność szkoły w zakresie krajoznawstwa i turystyki, w tym koszty przejazdu, zakwaterowania i wyżywienia kierownika i opiekunów wycieczek, mogą być finansowane ze środków pozabudżetowych, a w szczególności:</span><br />
                <span>1) Z odpłatności uczniów biorących udział w wycieczce.</span><br />
                <span>2) Ze środków pochodzących z działalności samorządu uczniowskiego i organizacji młodzieżowych działających na terenie szkoły.</span><br />
                <span>3) Ze środków wypracowanych przez uczniów.</span><br />
                <span>4) Ze środków przekazanych przez radę rodziców, a także osoby fizyczne i prawne.</span><br />
                <span>- Rodzice, którzy zadeklarowali udział swojego dziecka w wycieczce, a następnie deklarację tę wycofali, zobowiązani są do pokrycia strat, jakie powstały z tego tytułu.</span><br />
                <span>- Kierownik oraz opiekunowie nie ponoszą kosztów udziału w wycieczce.</span><br />
                <span>- Kierownik wycieczki odpowiada za terminowe regulowanie zobowiązań finansowych z nią związanych.</span><br />
                <span>- Rozliczenia wycieczki dokonuje kierownik, określając sposób zagospodarowania nadwyżki, względnie uzupełnienia niedoboru.</span><br />
                <span>- Dowodami finansowymi są:</span><br />
                <span>1) Listy wpłat z podpisami rodziców/uczniów.</span><br />
                <span>2) Rachunki, faktury.</span><br />
                <span>3) Bilety, karnety itp.</span><br />
            </div>
            
            <div class="pracaBiblio">
                <h3>§16:</h3>
                <h4>Dokumentacja wycieczki</h4>
                <span>- Na dokumentację wycieczki składają się:</span><br />
                <span>1) Karta wycieczki z harmonogramem na każdy dzień – załącznik 1.</span><br />
                <span>2) Lista uczestników wycieczki z telefonem do rodziców oraz numerem polisy ubezpieczeniowej – załącznik 2.</span><br />
                <span>3) Pisemna zgoda rodziców na wyjazd/wyjście dziecka – załącznik 3, w przypadku zawodów sportowych - załącznik 3a.</span><br />
                <span>4) Regulamin wycieczki – załącznik 4.</span><br />
                <span>5) Oświadczenie kierownika wycieczki – załącznik 5.</span><br />
                <span>6) Rozliczenie finansowe wycieczki – załącznik 6.</span><br />
                <span>7) Informacja o organizacji wycieczki za granicą – załącznik 7.</span><br />
                <span>8) Upoważnienie do przetwarzania danych osobowych – załącznik 8.</span><br />
            </div>
            
            <div class="pracaBiblio">
                <h3>§17:</h3>
                <h4>Zadania dyrektora:</h4>
                <span>- Wyznacza kierownika i opiekunów wycieczki.</span><br />
                <span>- Zatwierdza dokumenty.</span><br />
                <span>- Współpracuje z radą rodziców w zakresie organizacji i finansowania wycieczek.</span><br />
                <span>- Zawiadamia organ sprawujący nadzór pedagogiczny w przypadku planowanej wycieczki kilkudniowej krajowej i zagranicznej (załącznik 7).</span><br />
                <span>- Zatwierdza rozliczenie finansowe wycieczki.</span><br />
            </div>

            <div class="pracaBiblio">
                <h3>§18:</h3>
                <h4>Zalecane normy bezpieczeństwa</h4>

                <span>- Wycieczka autokarowa:</span><br />
                <span>1) Liczebność grupy - 1 opiekun na 15 uczniów (nie licząc kierowcy i pilota/przewodnika); i dostosowana do możliwości technicznych autokaru/liczby miejsc.</span><br />
                <span>2) Miejsca przy drzwiach zajmują osoby dorosłe (opiekunowie).</span><br />
                <span>3) Przejścia w autokarze muszą być wolne, nie mogą znajdować się tam dodatkowe miejsca do siedzenia.</span><br />
                <span>4) Autokar musi być oznakowany – tablica „przewóz dzieci”.</span><br />
                <span>5) Kierowca powinien posiadać świadectwo kwalifikacyjne kierowcy zawodowego oraz potwierdzenie sprawności technicznej autokaru - ważne przez 6 miesięcy</span><br />
                <span>⌊ Na prośbę rodziców/opiekunów prawnych (wystosowaną nie później niż 2 tygodnie przed planowaną wycieczką) możliwe jest sprawdzenie w dniu wyjazdu stanu technicznego pojazdu/autokaru na stacji diagnostycznej oraz stanu trzeźwości kierowcy przez policję.</span><br />
                <span>6) Maksymalny czas jazdy jednego kierowcy bez przerwy - 4 godziny 30 minut, czas przerwy w jeździe – zalecane 45 minut.</span><br />
                <span>7) Postoje mogą odbywać się tylko w miejscach do tego wyznaczonych, tj. na oznakowanych parkingach.</span><br />
                <span>8) Po każdej przerwie w podróży należy sprawdzić stan liczebny dzieci.</span><br />
                <span>9) Obowiązkiem opiekunów jest dopilnowanie dzieci w czasie jazdy (uczniowie nie mogą chodzić po autokarze, siedzieć tyłem, na oparciu, wyrzucać śmieci przez okno, itd.).</span><hr />
                

                <span>- Wycieczka piesza:</span><br />
                <span>1) W czasie marszu jeden opiekun prowadzi grupę, drugi idzie na końcu.</span><br />
                <span>2) Uczestnicy powinni posiadać ubiór (szczególnie buty) odpowiedni do miejsca i warunków atmosferycznych.</span><br />
                <span>3) W mieście należy poruszać się po chodnikach, a poza miastem - lewą stroną drogi, pojedynczo, ustępując z drogi nadjeżdżającym pojazdom.</span><br />
                <span>4) W lesie należy poruszać się po znakowanych szlakach turystycznych (na terenie parków narodowych lub krajobrazowych - wyłącznie) lub wyznaczonych ścieżkach.</span><br />
                <span>5) Opiekun powinien posiadać mapę (wskazana jest znajomość terenu).</span><br />
                <span>6) Przed wyruszeniem sprzed szkoły uczestnicy powinni być poinformowani o zasadach poruszania się po drogach i po lesie.</span><hr />


                <span>- Wycieczka w góry:</span><br />
                <span>1) Wyruszając w góry należy dokładnie zaplanować trasę, uwzględniając wiek, a także płeć uczestników - dla młodzieży w wieku 13-14 lat nie powinna przekraczać 12-13 km, tj. 4-5 godz. marszu wraz z odpoczynkami. Dla młodzieży starszej długość trasy może ulec wydłużeniu.</span><br />
                <span>2) Na wędrówkę należy wyruszać wcześnie rano, aby przed zmrokiem dojść do miejsca noclegu.</span><br />
                <span>3) Liczebność grupy - 1 opiekun na 10 uczestników.</span><br />
                <span>4) Uczestnicy powinni posiadać odpowiedni ubiór - buty sznurowane, kurtka (ze względu na zmienność pogody w górach oraz spadek temperatury wraz ze wzrostem wysokości). Ekwipunek należy zabrać w plecaku.</span><br />
                <span>5) Wszyscy uczestnicy wycieczki powinni poruszać się wyłącznie po oznakowanych szlakach turystycznych, w ten sposób, że na początku (za przewodnikiem) ustawione są osoby słabsze kondycyjnie, a na końcu osoby najsilniejsze.</span><br />
                <span>6) Kolumnę otwiera i zamyka  osoba dorosła.</span><br />
                <span>7) Na terenach powyżej 1000 m n.p.m. wycieczkę prowadzi wyłącznie przewodnik górski lub przodownik turystyki górskiej.</span><br />
                <span>8) Na terenie parków narodowych wycieczki prowadzą wyłącznie upoważnieni przewodnicy górscy.</span><hr />


                <span>- Wycieczka rowerowa:</span><br />
                <span>1) Liczebność - 2 opiekunów na grupę 10-13 uczniów (zgodnie z prawem o ruchu drogowym w kolumnie rowerów nie może jechać więcej niż 15 osób).</span><br />
                <span>2) W przypadku kilku grup odstępy między nimi powinny wynosić co najmniej 200 metrów.</span><br />
                <span>3) Wszyscy uczniowie muszą posiadać kartę rowerową.</span><br />
                <span>4) Prowadzący wycieczkę powinien jechać na początku, za nim najsłabsi w grupie, następnie pozostali uczniowie, drugi opiekun na końcu grupy.</span><br />
                <span>5) Tempo jazdy powinno być dostosowane do możliwości najsłabszego uczestnika.</span><br />
                <span>6) Odstępy pomiędzy jadącymi do 5m.</span><br />
                <span>7) Uczestnicy wycieczki powinni jechać jeden za drugim (nie wolno jechać obok siebie), jak najbliżej prawej krawędzi drogi, zgodnie z przepisami ruchu drogowego.</span><br />
                <span>8) Opiekun wycieczki winien posiadać apteczkę pierwszej pomocy oraz narzędzia do ewentualnej naprawy rowerów, także zapasowe dętki i wentyle oraz pompkę.</span><br />
                <span>9) Uczestnicy powinni posiadać ubiór odpowiedni do jazdy oraz kask ochronny na głowę.</span><br />
                <span>10) Z boku tylnego bagażnika (z lewej strony) powinno być przymocowane tzw. ramię bezpieczeństwa ze światłem odblaskowym na końcu.</span><hr />

                
                <span>- Wycieczka turystyki kwalifikowanej:</span><br />
                <span>
                    Przepisy prawne nie definiują pojęcia „turystyka kwalifikowana”. W dostępnej literaturze turystyka kwalifikowana określana jest jako najwyższa forma specjalizacji turystycznej. Uprawianie jej wymaga specjalnego przygotowania psychofizycznego, zahartowania na trudy, umiejętności zachowania się w środowisku naturalnym i w obiektach turystycznych, a w niektórych przypadkach potwierdzonej przez właściwe organizacje umiejętności posługiwania się sprzętem turystycznym. W związku z tym,  organizując wycieczkę turystyki kwalifikowanej, dla bezpieczeństwa jej uczestników, należy zapewnić 1 opiekuna na grupę 10 uczniów. W szczególnych okolicznościach  konieczne jest zwiększenie liczby opiekunów przy uwzględnieniu m.in. ukształtowania terenu, wieku i aktualnego stanu zdrowia uczestników, predyspozycji psychofizycznych oraz aktualnych i przewidywanych warunków atmosferycznych.
                </span><br />
            </div>

            
            <div class="pracaBiblio">
                <h3>§19:</h3>
                <h4>Postanowienia końcowe:</h4>
                <span>- Uczniowie, którzy nie uczestniczą w  wycieczce organizowanej w dniach zajęć szkolnych mają obowiązek brać udział w zajęciach z wyznaczonym oddziałem.</span><br />
                <span>- Listę tych uczniów wychowawca dołącza do odpowiedniego dziennika lekcyjnego.</span><br />
                <span>- W razie wypadku uczestników wycieczki stosuje  się przepisy dotyczące  postępowania w razie wypadków w szkole.</span><br />
                <span>- W przypadku rażącego naruszenia regulaminu wycieczki przez uczestnika   kierownik wycieczki zawiadamia rodziców i dyrektora szkoły. Rodzice (prawni opiekunowie) zobowiązani są w takim przypadku do natychmiastowego odebrania dziecka z wycieczki.</span><br />
                <span>- W sprawach nieuregulowanych niniejszym regulaminem mają zastosowanie:</span><br />
                <span>1) Rozporządzenie Ministra Edukacji Narodowej i Sportu z dnia 31 grudnia 2002 r. w sprawie bezpieczeństwa i higieny w publicznych i niepublicznych szkołach i placówkach (Dz. U. z 2003 r.,  nr 6, poz. 69 ze zmianami).</span><br />
                <span>2) Rozporządzenie Ministra Edukacji Narodowej z dnia 1 czerwca 2018 r. w sprawie warunków i sposobu organizowania przez publiczne przedszkola, szkoły i placówki krajoznawstwa i turystyki (Dz. U z 2018r., poz. 1055).</span><br />
                <span>3) Statut Szkoły.</span><br />
                <span>4) Kodeks pracy.</span><br />
