<template>
    <div class="titleContainer shadow">
        <div class="title">
            <h1>Plan lekcji</h1>
        </div>
    </div>

    <div class="wydarzenie shadow">
        <div class="description">
            <h2>Rok Szkolny 2021/22</h2>
            
            <span>{{ page }} / {{ numPages }}</span><br />
            <!--Przyciski-->
            <button @click="leftPage();" class="przycisk shadow">
                <i class="fa-solid fa-caret-left"></i>
            </button>
            <button @click="rightPage();" class="przycisk shadow">
                <i class="fa-solid fa-caret-right"></i>
            </button>

            <!--PDF-->
            <div class="pdf">
                <pdf src="./pdf/plan-2021-22.pdf" :page="this.page" />
            </div>
            
            <!--Przyciski-->
            <button @click="leftPage();" class="przycisk shadow">
                <i class="fa-solid fa-caret-left"></i>
            </button>
            <button @click="rightPage();" class="przycisk shadow">
                <i class="fa-solid fa-caret-right"></i>
            </button>
        </div>
    </div>
</template>

<script>
import pdf from 'pdfvuer';

export default {
    name: 'PlanLekcji',
    data(){
        return{
            page: 1,
            numPages: 2
        }
    },
    components: {
        pdf,
    },
    methods:{
        leftPage(){
            if(this.page !== 1)
            {
                this.page--
            }
        },

        rightPage(){
            if(this.page !== this.numPages)
            {
                this.page++
            }
        }
    }
}
</script>